/* General reset */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

/* Platform container */
.platform {
    width: 80%; /* Initial width, will be dynamically updated */
    height: 80vh; /* Initial height, will be dynamically updated */
    max-width: 1000px; /* Maximum width of the platform */
    background-color: orange; /* Set background to orange */
    margin: 0 auto; /* Center the platform */
    padding: 20px;
    overflow-y: auto; /* Keep scrolling enabled */
    position: relative;
}

/* Calendar container (Grid layout) */
.calendar {
    display: grid;
    grid-gap: 10px;
    padding: 10px;
    height: 100%;
    width: 100%;
}

/* Styling for each square */
.square {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%; /* Make squares responsive */
    height: 100%; /* Make squares responsive */
    max-width: 100px; /* Max size for the squares */
    max-height: 100px; /* Max size for the squares */
    background-color: #f0f0f0;
    border: 2px solid #ccc;
    font-size: 20px;
    cursor: pointer;
    user-select: none;
    transition: background-color 0.3s;
}

.square:hover {
    background-color: #dcdcdc;
}

/* Restore button styling */
.restore-button {
    position: fixed;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
    padding: 10px 20px;
    font-size: 18px;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s;
    z-index: 10; /* Ensure button is on top */
}

.restore-button:hover {
    background-color: #45a049;
}
